/* Surcharges des skins liées à l'affichage RTL */
import {IReg, REG} from "lib/commons/registry";

export function ovrReg(reg: IReg<any>) {
	reg.overlaySkin("clusters-mgr", REG.LEVELAUTH_RTL_OVR, /* language=CSS */ `
	  #search {
		  background-position: right;
	  }
	`);
	reg.overlaySkin("instances-mgr", REG.LEVELAUTH_RTL_OVR, /* language=CSS */ `
	  #search {
		  background-position: right;
	  }
	`);
}